.sub-menu-wrap {
    display: block;
}

.wrap-good>.good-banner {
    margin-top: 78px;
    display: flex;

    img {
        width: 50%;
    }
}

.wrap-good h1 {
    text-align: center;
    font-size: 32px;
    margin: 80px 0 32px;

    big {
        font-size: 42px;
    }
}

.wrap-good {
    .card {
        max-width: 306px;
        margin-bottom: 80px;
    }

    .card-txt>p {
        font-size: 24px;
    }

    .card-txt>p:last-child {
        color: $dark-red;
    }

    .color {
        div {
            border-radius: 8px;
            width: 24px;
            height: 24px;
            background-color: $black;
        }

        div+div {
            margin-left: 8px;
        }

        div:first-child {
            background-color: $dark-red;
        }
    }
}

@media screen and (max-width: 768px) {
    .card-list>.card {
            max-width: 336px;
            margin-bottom: 80px;
        }
}

@media screen and (max-width: 375px) {
    .card-list {
        display: block;}
    .card-list>.card {
            max-width: 306px;
            margin-bottom: 80px;
        }
    .card:nth-child(n+7){
        display: none;
    }
}